import React, { Component } from "react";
import { Row, Col } from "reactstrap";

export default class Ship extends Component {
  render() {
    const shipName = this.props.shipName;
    const shipRoleList = this.props.shipRoleList;
    const shipImage = require("assets/img/ship_images/" + shipName + ".png");
    const shipJson = require("assets/json/ship/" + shipName + ".json");

    const antiairIcon = require("assets/img/icons/antiair.png");
    const flagIcon = require("assets/img/icons/flag.png");
    const tankIcon = require("assets/img/icons/tank.png");
    const healIcon = require("assets/img/icons/heal.png");

    const getShipRarityClass = () => {
      return "rarity-" + shipJson.rarity.toLowerCase().replace(/\s/g, "-");
    };

    const openModal = () => {
      this.props.openShipModal({
        shipName: shipName,
        shipImage: shipImage,
        shipJson: shipJson,
      });
    };

    return (
      <Col sm="2">
        <Row style={{ minHeight: "20px" }}>
          <Col>
            {shipRoleList.AA.includes(shipName) ? (
              <img
                data-tip="Ship with exceptional AntiAir"
                style={{ maxHeight: "20px" }}
                src={antiairIcon}
              ></img>
            ) : (
              ""
            )}
            {shipRoleList.FLAG.includes(shipName) ? (
              <img
                data-tip="Ship that perform the best with the Flagship position"
                className={"rounded"}
                style={{ maxHeight: "20px" }}
                src={flagIcon}
              ></img>
            ) : (
              ""
            )}
            {shipRoleList.TANK.includes(shipName) ? (
              <img
                data-tip="Ship with exceptional effective HP to serve as the Tank"
                className={"rounded"}
                style={{ maxHeight: "20px" }}
                src={tankIcon}
              ></img>
            ) : (
              ""
            )}
            {shipRoleList.HEAL.includes(shipName) ? (
              <img
                data-tip="Ship with a healing ability"
                className={"rounded"}
                style={{ maxHeight: "20px" }}
                src={healIcon}
              ></img>
            ) : (
              ""
            )}
          </Col>
        </Row>
        <img
          alt="..."
          className={
            "rounded img-raised ship-rarity-padding " + getShipRarityClass()
          }
          src={shipImage}
          style={{ cursor: "pointer" }}
          onClick={() => openModal()}
        ></img>
        <p className="category">{shipName}</p>
      </Col>
    );
  }
}
