import React, { Component } from "react";

import { Row, Col, Label, Input } from "reactstrap";

export default class FilterBar extends Component {
  render() {
    console.log(this.props.shipRoleList);
    const shipRoleList = this.props.shipRoleList;
    const setFilteredAA = this.props.setFilteredAA;
    const setFilteredFlag = this.props.setFilteredFlag;
    const setFilteredTank = this.props.setFilteredTank;
    const setFilteredHeal = this.props.setFilteredHeal;
    const filteredAA = this.props.filteredAA;
    const filteredFlag = this.props.filteredFlag;
    const filteredTank = this.props.filteredTank;
    const filteredHeal = this.props.filteredHeal;

    return (
      <Row>
        {shipRoleList.AA.length !== 0 ? (
          <Col sm="3">
            <Label check>
              <Input
                defaultChecked
                type="checkbox"
                onChange={() => {
                  setFilteredAA(!filteredAA);
                }}
              ></Input>
              <span className="form-check-sign"></span>
              AA Specialized Ships
            </Label>
          </Col>
        ) : (
          ""
        )}
        {shipRoleList.FLAG.length !== 0 ? (
          <Col sm="3">
            <Label check>
              <Input
                defaultChecked
                type="checkbox"
                onChange={() => {
                  setFilteredFlag(!filteredFlag);
                }}
              ></Input>
              <span className="form-check-sign"></span>
              Flag-recommended Ships
            </Label>
          </Col>
        ) : (
          ""
        )}
        {shipRoleList.TANK.length !== 0 ? (
          <Col sm="3">
            <Label check>
              <Input
                defaultChecked
                type="checkbox"
                onChange={() => {
                  setFilteredTank(!filteredTank);
                }}
              ></Input>
              <span className="form-check-sign"></span>
              Tank-centric Ships
            </Label>
          </Col>
        ) : (
          ""
        )}
        {shipRoleList.HEAL.length !== 0 ? (
          <Col sm="3">
            <Label check>
              <Input
                defaultChecked
                type="checkbox"
                onChange={() => {
                  setFilteredHeal(!filteredHeal);
                }}
              ></Input>
              <span className="form-check-sign"></span>
              Healer Ships
            </Label>
          </Col>
        ) : (
          ""
        )}
      </Row>
    );
  }
}
